.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.top-container {
  display: flex;
  align-items: bottom;
}

.button-container {
  width: 100px
}

.input-container {
  width: 50vw ;
}

.content-container {
  border: 5px black solid;
  margin: 0 4px 0 4px;
  height: 80vh; 
  overflow: auto;
}

::-webkit-scrollbar {
  width: 8px;
  height: 10px;

}

::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background: #c2c9d2;
}